.NavContainer{
    color: white;
    position: relative;
    overflow-x: clip;
    background: linear-gradient(0deg, rgba(255,255,255,0) 0%, rgba(0, 0, 0, 0.7) 100%);
    padding: 3vh;
    padding-bottom: 5vh;
}

a{
    text-decoration: none;
    padding: 1vh;
    font-size: 1.8vh;
    color: white;
}

a:hover{
    font-style: italic;
    background-color: rgba(255, 255, 0, 0.25);
}

.NavTitle{
    font-size: 3vh;
    margin-bottom: 4vh;
    font-weight: 600;
}

.TrashBackgroundImage{
    margin: -5vh;
    width: 110%;
    position: absolute;
    z-index: -1;
}