.HeaderContainer{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 4vh;
    margin-bottom: 4vh;
    padding: 5vh;
    color: white;
    background: rgb(0,0,0);
    background: radial-gradient(circle, rgba(0,0,0,0.2) 0%, rgba(255,255,255,0) 50%);
}

.HeaderTitle{
    font-size: 5vh;
    font-style: oblique;
    margin-bottom: -4vh;
}
.HeaderSubTitle{
    font-size: 5vh;
    font-style: oblique;
}