.BodyContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(7,55,99,1);
    padding: 3vh;
    padding: 2vh;
}

.DisplayTextContainer{
    color: white;
}

.StateTextButton{
    background-color: transparent;
    padding: 0.5vh;
    border: none;
    color: white;
    font-size: 2.5vh;
    font-weight: 800;
}

.StateTextButton:hover{
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    border: none;
    color: white;
    font-size: 2.5vh;
    font-weight: 800;
}

.StateText{
    font-size: 2vh;
    font-style: oblique;
    font-weight: 400;
}